}

.gradient-border-circular {
  position: relative;
  border-radius: 8px;
  overflow: hidden;
  padding: 4px;
}

.gradient-border-circular::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  border-radius: inherit;
  background: radial-gradient(at 0% 0%, #237674, #32c8c7);
  background-size: 200% 200%;
  background-position: center;

  animation: pulseGlow 1.5s ease-in-out infinite alternate;
}

@keyframes pulseGlow {
  0% {
    transform: scale(1); /* Skala normal */
    filter: brightness(1); /* Kecerahan normal */
    opacity: 1; /* Opasitas normal */
  }
  50% {
    transform: scale(1.02); /* Sedikit membesar untuk efek denyutan */
    filter: brightness(1.2); /* Sedikit lebih cerah (efek glow) */
    opacity: 0.95; /* Sedikit transparan (opsional, untuk soft glow) */
  }
  100% {
    transform: scale(1); /* Kembali ke skala normal */
    filter: brightness(1); /* Kembali ke kecerahan normal */
    opacity: 1; /* Kembali ke opasitas normal */
  }
}

